import logo from './logo.svg';
import './App.css';
import React from 'react';

const pronouns = [["He", "Him", "His", "His", "Himself"], ["She", "Her", "Her", "Hers", "Herself"], ["They", "Them", "Their", "Theirs", "Themself"], ["It", "It", "Its", "Its", "Itself"], ["One", "One", "One's", "One's", "Oneself"], ["Thon", "Thon", "Thons", "Thon's", "Thonself"], ["E", "Em", "Es", "Ems", "Emself"], ["Ae", "Aer", "Aer", "Aers", "Aerself"], ["Co", "Co", "Cos", "Co's", "Coself"], ["Ve", "Ver", "Vis", "Vers", "Verself"], ["Vi", "Vir", "Vis", "Virs", "Virself"], ["Xe", "Xem", "Xyr", "Xyrs", "Xemself"], ["Per", "Per", "Per", "Pers", "Perself"], ["Ey", "Em", "Eir", "Eirs", "Emself"], ["Hu", "Hum", "Hus", "Hus", "Huself"], ["E", "Em", "Eir", "Eirs", "Emself"], ["Ze", "Zir", "Zir", "Zirs", "Zirself"], ["Fae", "Faer", "Faer", "Faers", "Faerself"]];

const pronounList = pronouns.map((item, index) =>
	<option value={index.toString()}>{item.join("/")}</option>
);

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = { input: "hi", find: 0, replace: 0 };
		this.handleChange = this.handleChange.bind(this)
		this.replaceProns = this.replaceProns.bind(this)
		this.handleFindSelect = this.handleFindSelect.bind(this)
		this.handleReplaceSelect = this.handleReplaceSelect.bind(this)
	}

	handleChange(event) {
		this.setState({
			input: event.target.value
		}, () => {
			this.replaceProns()
		});
	}

	replaceProns() {
		console.log(this.state.input)
		let outputString = this.state.input;
		for (let i = 4; i >= 0; i--) {
			outputString = outputString.replaceAll(new RegExp(`\\b${pronouns[this.state.find][i]}`, "g"), pronouns[this.state.replace][i])
		}
		for (let i = 4; i >= 0; i--) {
			outputString = outputString.replaceAll(new RegExp(`\\b${pronouns[this.state.find][i].toLowerCase()}`, "g"), pronouns[this.state.replace][i].toLowerCase())
		}
		for (let i = 4; i >= 0; i--) {
			outputString = outputString.replaceAll(new RegExp(`\\b${pronouns[this.state.find][i].toUpperCase()}`, "g"), pronouns[this.state.replace][i].toUpperCase())
		}
		this.setState({ output: outputString });
	}

	handleFindSelect(event) {
		this.setState({
			find: event.target.value
		}, () => {
			this.replaceProns()
		});
	}

	handleReplaceSelect(event) {
		this.setState({
			replace: event.target.value
		}, () => {
			this.replaceProns()
		});
	}

	render() {
		return (
			<div class="app">
				<textarea onChange={this.handleChange} />

				<div class="menus">
					<select id="find" onChange={this.handleFindSelect}>
						{pronounList}
						<option value="custom" disabled>Was too lazy to add a custom option, sorry!</option>
					</select>
					<span>V</span>
					<select id="replace" onChange={this.handleReplaceSelect}>
						{pronounList}
						<option value="custom" disabled>Was too lazy to add a custom option, sorry!</option>
					</select>
				</div>

				<textarea value={this.state.output} disabled></textarea>
			</div>
		);
	}
}

export default App;
